// 此文件专门负责项目的路由
import Vue from 'vue'
import VueRouter from "vue-router"

Vue.use(VueRouter)

// 引入组件
import LoginDemo from '../views/users/LoginDemo'
import RegisterDemo from '../views/users/RegisterDemo'
import HomeDemo from '../views/users/HomeDemo'

import StudentPage from '../views/home/StudentPage'
import TeacherPage from '../views/home/TeacherPage'

import MaterialList from '../views/material/MaterialList'
import MaterialDemo from '../views/material/MaterialDemo'
import MaterialAdd from '../views/material/MaterialAdd'
import MaterialView from '../views/material/MaterialView'

import ScoresList from '../views/scores/ScoresList'
import ScoresDemo from '../views/scores/ScoresDemo'
import ScoresAdd from '../views/scores/ScoresAdd'
import ScoresView from '../views/scores/ScoresView'
import ScoresDelete from '../views/scores/ScoresDelete'
import { Message } from "element-ui";

// 创建并暴露一个路由器
const router = new VueRouter({
    mode: 'history',    // 路由模式，该模式不会在地址中显示井号#
    routes: [
        {
            path: '/',          // 路径
            redirect: '/login'  // 重定向
        },

        {
            path: '/login',     // 路径
            component: LoginDemo    // 跳转到的组件
        },

        {
            path: '/register',     // 路径
            component: RegisterDemo    // 跳转到的组件
        },

        {
          path: '/student',
          component: StudentPage,
          name:'student',
          children: [
            {
              path: 'HomeDemo',
              component: HomeDemo,
              name:'HomeDemo'
            },
            {
              path: 'MaterialList',
              component: MaterialList,
              name:'MaterialList'
            },
            {
              path: 'ScoresList',
              component: ScoresList,
              name:'ScoresList'
            }
          ]
        },

        {
          path: '/teacher',
          component: TeacherPage,
          name:'teacher',
          children: [
            {
              path: 'HomeDemo',
              component: HomeDemo,
              name:'HomeDemo',
            },
            {
              path: 'MaterialDemo',
              component: MaterialDemo,
              name:'MaterialDemo',
              children:[
                {
                  path: 'MaterialAdd',
                  component: MaterialAdd,
                  name:'MaterialAdd'
                },
                {
                  path: 'MaterialView',
                  component: MaterialView,
                  name:'MaterialView'
                }
              ]
            },
            {
              path: 'ScoresDemo',
              component: ScoresDemo,
              name:'ScoresDemo',
              children:[
                {
                  path: 'ScoresAdd',
                  component: ScoresAdd,
                  name:'ScoresAdd'
                },
                {
                  path: 'ScoresView',
                  component: ScoresView,
                  name:'ScoresView'
                },
                {
                  path: 'ScoresDelete',
                  component: ScoresDelete,
                  name:'ScoresDelete'
                }
              ]
            }
          ]
        }
    ]
})

//导航守卫，前置处理
router.beforeEach((to, from, next) => {
    let isAuthenticated = !!sessionStorage.getItem('userInfo')
    // 如果路由要跳转到除了登录和注册的界面的话就判断是否已经登录，如果没有登录就强制跳到登录界面
    if (to.path !== '/login' && to.path !== '/register' && !isAuthenticated) {
        next({ path: '/login' })
        Message({
            message: '请先登录！',
            type: "warning",
        });
    } else next()
})

export default router;