<template>
    <div>
        <el-card class="box-card">
            <h2>注册</h2>
            <el-form
                    :model="ruleForm"
                    status-icon
                    :rules="rules"
                    ref="ruleForm"
                    label-position="left"
                    label-width="80px"
                    class="register-form"
            >
                <!-- 表单项：用户名 -->
                <el-form-item label="用户名" prop="uname">
                    <el-input v-model="ruleForm.uname"></el-input>
                </el-form-item>
                <!-- 表单项：密码 -->
                <el-form-item label="密码" prop="password">
                    <el-input
                            type="password"
                            v-model="ruleForm.password"
                            autocomplete="off"
                    ></el-input>
                </el-form-item>
                <!-- 表单项：用户身份 -->
                <el-form-item label="用户身份" prop="user_status">
                    <el-radio-group v-model="ruleForm.user_status">
                        <el-radio :label="0">老师</el-radio>
                        <el-radio :label="1">学生</el-radio>
                    </el-radio-group>
                </el-form-item>
                <!-- 表单项：电话 -->
                <el-form-item label="电话" prop="phone">
                    <el-input type="text" v-model="ruleForm.phone"></el-input>
                </el-form-item>
                <!-- 表单项：性别 -->
                <el-form-item label="性别" prop="gender">
                    <el-radio-group v-model="ruleForm.gender">
                        <el-radio label="man">男</el-radio>
                        <el-radio label="woman">女</el-radio>
                    </el-radio-group>
                </el-form-item>
                <!-- 表单项：班级 -->
                <el-form-item label="班级" prop="class_id">
                    <el-radio-group v-model="ruleForm.class_id">
                        <el-radio :label="1">1班</el-radio>
                        <el-radio :label="2">2班</el-radio>
                        <el-radio :label="3">3班</el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>

            <!-- 按钮组 -->
            <div class="btnGroup">
                <!-- 提交按钮 -->
                <el-button type="primary" @click="submitForm('ruleForm')"  v-loading="loading">提交</el-button>
                <!-- 重置按钮 -->
                <el-button @click="resetForm('ruleForm')">重置</el-button>
                <!-- 返回按钮 -->
                <el-button @click="goBack">返回</el-button>
            </div>
        </el-card>
    </div>
</template>

<script>
// 导出 Vue 组件
export default {
  data() {
    return {
      // 表单数据模型
      ruleForm: {
        uname: "",
        password: "",
        user_status:0,
        phone:"",
        gender:"",
        class_id:0,
      },
      // 表单验证规则
      rules: {
        uname: [{ required: true, message: "用户名不能为空！", trigger: "blur" },],
        password: [{ required: true, message: "密码不能为空！", trigger: "blur" },],
        user_status: [{ required: true, message: "身份不能为空！", trigger: "blur" },],
        phone: [{ required: true, message: "电话不能为空！", trigger: "blur" },],
        gender: [{ required: true, message: "性别不能为空！", trigger: "blur" },],
        class_id: [{ required: true, message: "班级不能为空！", trigger: "blur" },],
      },
      loading: false  // 加载状态
    };
  },

  // 组件的方法
  methods: {
    // 提交表单
    submitForm(formName) {
      // 校验表单数据
      this.$refs[formName].validate((valid) => {
        this.loading = true;  // 提交按钮显示加载动画
        // 表单数据校验通过
        if (valid) {
          let _this = this;
          // 将选中的值转换为数字类型
          _this.ruleForm.user_status = parseInt(_this.ruleForm.user_status);
          // 将选中的值转换为数字类型
          _this.ruleForm.class_id = parseInt(_this.ruleForm.class_id);
          // axios 向后端发起请求
          this.axios({
            method: "post",             // 请求方法
            url: "/user/register",  // 请求地址
            headers: {                  // 请求头
              "Content-Type": "application/json",
            },
            data: {     // 请求参数
              uname: _this.ruleForm.uname,
              password: _this.ruleForm.password,
              user_status: _this.ruleForm.user_status,
              phone: _this.ruleForm.phone,
              gender: _this.ruleForm.gender,
              class_id: _this.ruleForm.class_id,
            },
          }).then((res) => { // 当收到后端的响应时执行该括号内的代码，res 为响应信息，也就是后端返回的信息
            if (res.data.code === '0') {  // 当响应的编码为 0 时，说明注册成功
              // 显示后端响应的成功信息
              this.$message({
                message: res.data.msg,
                type: "success",
              });
            }else{  // 当响应的编码不为 0 时，说明注册失败
              // 显示后端响应的失败信息
              this.$message({
                message: res.data.msg,
                type: "warning",
              });
            }
            _this.loading = false;  // 不管响应成功还是失败，收到后端响应的消息后就不再让登录按钮显示加载动画了
            console.log(res);
          });
        } else { // 如果账号或密码有一个没填，就直接提示必填，不向后端请求
          console.log("error submit!!");
          this.loading = false;
          return false;
        }
      });
    },
    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 返回上一页
    goBack() {
      this.$router.go(-1);
    },
  },
};
</script>

<style scoped>
/* 设置登录面板居中，宽度为400px */
.box-card {
  margin: auto auto;
  width: 400px;
}
/* 设置登录面板中的表单居中 */
.register-form {
  margin: auto auto;
}
</style>