<template>
    <div class="score-list">
        <h1>删除成绩</h1>
        <table>
            <thead>
            <tr>
                <th>学生ID</th>
                <th>班级ID</th>
                <th>课程ID</th>
                <th>期末成绩</th>
                <th>期中成绩</th>
                <th>总成绩</th>
                <th>操作</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="score in scores" :key="score.id">
                <td>{{ score.uname }}</td>
                <td>{{ score.class_id }}</td>
                <td>{{ score.course_id }}</td>
                <td>{{ score.final_score }}</td>
                <td>{{ score.mid_score }}</td>
                <td>{{ score.total_score }}</td>
                <!-- 删除按钮 -->
                <td>
                    <button @click="deleteScore(score.uname, score.course_id)">删除成绩</button>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
export default {
  data() {
    return {
      scores: []
    }
  },

  mounted() {
    this.fetchScores();
  },

  methods: {
    fetchScores() {
      // 发送AJAX请求获取成绩数据
      fetch('/api/user/scorelistall')
        .then(response => response.json())
        .then(data => {
          this.scores = data; // 将返回的成绩数据赋值给scores
        })
        .catch(error => {
          console.error('Error:', error);
        });
    },

    deleteScore(uname, course_id) {
      fetch(`/api/user/scoredelete?uname=${uname}&course_id=${course_id}`)
        .then(response => response.json())
        .then(data => {
          if (data === 1) {
             this.$message({
                message: "删除成绩成功",
                type: "success",
             });
            this.fetchScores();// 删除成功，刷新成绩列表
          } else {
            this.$message({
               message: "删除成绩失败",
               type: "success",
            });
            console.error('删除成绩失败');
          }
        })
        .catch(error => {
          console.error('Error:', error);
        });
    }
  }
}
</script>

<style>
.score-list {
  max-width: 800px;
  margin: 0 auto;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  padding: 10px;
  text-align: center;
  border: 1px solid #ccc;
}

thead {
  background-color: #f5f5f5;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}
</style>
