<template>
    <div>
        <h2>欢迎{{ user.uname }}！您的 uid 为{{ user.uid }}</h2>

        <el-table :data="userTableData" border>
            <el-table-column prop="uname" label="用户名"></el-table-column>
            <el-table-column prop="password" label="密码"></el-table-column>
            <el-table-column prop="phone" label="电话"></el-table-column>
            <el-table-column prop="gender" label="性别"></el-table-column>
            <el-table-column prop="class_id" label="学院"></el-table-column>
        </el-table>
        <br>
        <el-button @click="logout"> 退出登录 </el-button>
    </div>
</template>

<script>
export default {
  data() {
    return {
      user: {
        uname: "",
        uid: null,
      },
      userTableData: [],
    };
  },
  methods: {
    logout(){
      // 移除本地用户登录信息
      sessionStorage.removeItem('userInfo');
      // 跳转页面到登录页
      this.$router.push('/login');
    }
  },
  mounted() {
    if (sessionStorage.getItem('userInfo')) {
      // 将用户信息存储到sessionStorage中
      this.user = JSON.parse(sessionStorage.getItem('userInfo'));
      // 将用户信息转换为表格数据格式
      this.userTableData = [
        {
          uname: this.user.uname,
          password: this.user.password,
          phone: this.user.phone,
          gender: this.user.gender,
          class_id: this.user.class_id,
        },
      ];
    }
  },
};
</script>

