<template>
    <div class="file-list">
        <h1>文件查询与下载</h1>
        <div class="search-bar">
            <label>资料类型：</label>
            <el-radio-group v-model="searchName">
                <el-radio label="teachplan">教学计划</el-radio>
                <el-radio label="teachprecis">教学大纲</el-radio>
                <el-radio label="courseware">课件</el-radio>
                <el-radio label="learnprofile">学习资料</el-radio>
                <el-radio label="homework">练习试题</el-radio>
            </el-radio-group>
            &nbsp;&nbsp;
            <button @click="fetchFiles">搜索</button>
        </div>
        <table>
            <thead>
            <tr>
                <th>文件ID</th>
                <th>文件名称</th>
                <th>存储路径</th>
                <th>文件类型</th>
                <th>操作</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="file in files" :key="file.id">
                <td>{{ file.fileid }}</td>
                <td>{{ file.filename }}</td>
                <td>{{ file.filepath }}</td>
                <td>{{ file.fileblock }}</td>
                <!-- 下载按钮 -->
                <td>
                    <button @click="downloadFile(file.fileid)">下载文件</button>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
export default {
  data() {
    return {
      files: [],
      searchName: ''
    }
  },
  mounted() {
    this.fetchFiles();
  },

  methods: {
    fetchFiles() {
      // 发送AJAX请求获取成绩数据
      fetch(`/downloadfileview?option=${this.searchName}`)
        .then(response => response.json())
        .then(data => {
          this.files = data; // 将返回的文件列表数据赋值给files
        })
        .catch(error => {
          console.error('Error:', error);
        });
    },

    downloadFile(fileid) {
      fetch(`/downloadfile?fileid=${fileid}`, {
        method: 'GET',
        responseType: 'blob' // 指定响应类型为 blob
      })
        .then(response => {
          if (response.ok) {
            return response.blob();
          } else {
            throw new Error('文件下载失败');
          }
        })
        .then(data => {
          // 创建一个下载链接
          const downloadUrl = URL.createObjectURL(data);
          const link = document.createElement('a');
          link.href = downloadUrl;
          link.download = ''; // 可以通过设置 download 属性来指定下载文件的名称
          link.click(); // 触发下载
          URL.revokeObjectURL(downloadUrl); // 释放对象 URL 资源
          this.$message({
            message: '文件下载成功',
            type: 'success',
          });
        })
        .catch(error => {
          console.error('Error:', error);
          this.$message({
            message: '文件下载失败',
            type: 'error',
          });
        });
    }
  }
}
</script>

<style>
.file-list {
  max-width: 800px;
  margin: 0 auto;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.search-bar {
  margin-bottom: 20px;
}

.search-bar label {
  margin-right: 10px;
}

.search-bar input {
  padding: 5px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  padding: 10px;
  text-align: center;
  border: 1px solid #ccc;
}

thead {
  background-color: #f5f5f5;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}
</style>