<template>
    <div>
        <el-card class="box-card">
            <h2>文件上传</h2>
            <el-form
                    :model="ruleForm"
                    status-icon
                    :rules="rules"
                    ref="ruleForm"
                    label-position="left"
                    label-width="80px"
                    class="filesAdd-form"
            >
                <!-- 表单项：资料类型 -->
                <el-form-item label="资料类型" prop="fileblock">
                    <el-radio-group v-model="ruleForm.fileblock">
                        <el-radio label="teachplan">教学计划</el-radio>
                        <el-radio label="teachprecis">教学大纲</el-radio>
                        <el-radio label="courseware">课件</el-radio>
                        <el-radio label="learnprofile">学习资料</el-radio>
                        <el-radio label="homework">练习试题</el-radio>
                    </el-radio-group>
                </el-form-item>

                <!-- 文件选择按钮 -->
                <el-form-item>
                    <input type="file" ref="fileInput" style="display: none;" @change="handleFileUpload"/>
                    <el-button size="small" type="primary" @click="$refs.fileInput.click()">选择文件</el-button>
                    <span v-if="selectedFile" class="selected-file">{{ selectedFile.name }}</span>
                    <span v-else class="file-placeholder">&nbsp;&nbsp;请选择文件</span>
                </el-form-item>

                <!-- 按钮组 -->
                <div class="btnGroup">
                    <!-- 上传文件按钮 -->
                    <el-button type="primary" @click="submitForm('ruleForm')" v-loading="loading">上传文件</el-button>
                    <!-- 重置按钮 -->
                    <el-button @click="resetForm('ruleForm')">重置</el-button>
                </div>
            </el-form>
        </el-card>
    </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      ruleForm: {
        fileblock: '',
      },
      fileList: [],
      selectedFile: null,
      fileContent: '',
    };
  },
  methods: {
    handleFileUpload(event) {
      this.selectedFile = event.target.files[0];
      //使用FileReader对象来读取文件内容，并将内容存储到fileContent属性中
      const reader = new FileReader();
      reader.onload = (e) => {
        this.fileContent = e.target.result;
      };
      reader.readAsText(this.selectedFile);
    },

    submitForm(formName) {
      if (!this.selectedFile || !this.ruleForm.fileblock) {
        alert('请选择文件和资料类型');
        return;
      }
      const formData = new FormData();
      formData.append('file', this.selectedFile);
      formData.append('option', this.ruleForm.fileblock);
      axios
        .post('/uploadfile', formData)
        .then(response => {
          const result = response.data;
          if (result === 2) {
            alert('文件和资料类型不能为空');
          } else if (result === 1) {
            alert('文件上传成功');
            // 文件信息处理
            const fileInfo = {
              name: this.selectedFile.name,
              content: this.fileContent,
            };
            this.fileList.push(fileInfo);
          } else {
            alert('文件上传失败，请稍后再试');
          }
        })
        .catch(error => {
          console.log(error);
        });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.selectedFile = null;
    },
  },
};
</script>

<style scoped>
/* 设置登录面板居中，宽度为400px */
.box-card {
  margin: auto auto;
  width: 800px;
}
/* 设置登录面板中的表单居中 */
.filesAdd-form {
  margin: auto auto;
}

.selected-file {
  display: inline-block;
  padding: 4px 8px;
  margin-left: 10px;
  background-color: #f3f3f3;
  border-radius: 4px;
  font-weight: bold;
}
</style>