<template>
    <div>
        <el-card class="box-card">
            <h2>登录</h2>

            <el-form
                    :model="ruleForm"
                    status-icon
                    :rules="rules"
                    ref="ruleForm"
                    label-position="left"
                    label-width="70px"
                    class="login-form"
            >
                <!-- 表单项：用户名 -->
                <el-form-item label="用户名" prop="uname">
                    <el-input type="text" v-model="ruleForm.uname"></el-input>
                </el-form-item>
                <!-- 表单项：密码 -->
                <el-form-item label="密码" prop="password">
                    <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>

            <!-- 按钮组 -->
            <div class="btnGroup">
                <!-- 登录按钮 -->
                <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                <!-- 重置按钮 -->
                <el-button @click="resetForm('ruleForm')">重置</el-button>
                <!-- 注册按钮 -->
                <router-link to="/register">
                    <el-button style="margin-left:10px">注册</el-button>
                </router-link>
            </div>
        </el-card>
    </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      // 表单数据模型
      ruleForm: {
        uname: "",
        password: "",
      },
      // 表单验证规则
      rules: {
        uname: [{ required: true, message: "用户名不能为空！", trigger: "blur" }],
        password: [{ required: true, message: "密码不能为空！", trigger: "blur" }],
      },
    };
  },

  // 组件的方法
  methods: {
    submitForm(formName) {
      // 验证表单中的账号密码是否有效
      this.$refs[formName].validate((valid) => {
        this.loading = true;// 点击登录后，让登录按钮开始转圈圈（展示加载动画）
        // 如果经过校验，账号密码都不为空，则发送请求到后端登录接口
        if (valid) {
          let _this = this;
          // 使用 axios 将登录信息发送到后端
          this.axios({
            method: "post",                       // 请求方法
            url: "/user/login",               // 请求地址
            headers: {                            // 请求头
              "Content-Type": "application/x-www-form-urlencoded",
            },
            params: {
                uname: _this.ruleForm.uname,
                password: _this.ruleForm.password,
            }
          }).then((res) => { // 当收到后端的响应时执行该括号内的代码，res 为响应信息，也就是后端返回的信息
            if (res.data.code === "0") {  // 当响应的编码为 0 时，说明登录成功
              // 将用户信息存储到sessionStorage中
              sessionStorage.setItem("userInfo", JSON.stringify(res.data.data));
              if(res.data.data.user_status===0){
                  // 跳转页面到教师端首页
                  this.$router.push('/teacher');
              }
              if(res.data.data.user_status===1){
                  // 跳转页面到学生端首页
                  this.$router.push('/student');
              }
              // 显示后端响应的成功信息
              this.$message({
                message: res.data.msg,
                type: "success",
              });
            } else {  // 当响应的编码不为 0 时，说明登录失败
              // 显示后端响应的失败信息
              this.$message({
                message: res.data.msg,
                type: "warning",
              });
            }
            // 不管响应成功还是失败，收到后端响应的消息后就不再让登录按钮显示加载动画了
            _this.loading = false;
            console.log(res);
          });
        } else {    // 如果账号或密码有一个没填，就直接提示必填，不向后端请求
          this.$message.error('请输入所有信息！');
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style scoped>
.box-card {
  margin: auto auto;
  width: 400px;
}
.login-form {
  margin: auto auto;
}
</style>