<template>
    <div class="file-list">
        <h1>文件查询与删除</h1>
        <div class="search-bar">
            <label>资料类型：</label>
            <el-radio-group v-model="searchName">
                <el-radio label="teachplan">教学计划</el-radio>
                <el-radio label="teachprecis">教学大纲</el-radio>
                <el-radio label="courseware">课件</el-radio>
                <el-radio label="learnprofile">学习资料</el-radio>
                <el-radio label="homework">练习试题</el-radio>
            </el-radio-group>
            &nbsp;&nbsp;
            <button @click="fetchFiles">搜索</button>
        </div>
        <table>
            <thead>
            <tr>
                <th>文件名称</th>
                <th>存储路径</th>
                <th>文件类型</th>
                <th>操作</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="file in files" :key="file.id">
                <td>{{ file.filename }}</td>
                <td>{{ file.filepath }}</td>
                <td>{{ file.fileblock }}</td>
                <!-- 删除按钮 -->
                <td>
                    <button @click="deleteFile(file.fileid)">删除文件</button>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
export default {
  data() {
    return {
      files: [],
      searchName: ''
    }
  },

  mounted() {
    this.fetchFiles();
  },

  methods: {
    fetchFiles() {
      // 发送AJAX请求获取成绩数据
      fetch(`/viewfile?option=${this.searchName}`)
        .then(response => response.json())
        .then(data => {
          this.files = data; // 将返回的文件列表数据赋值给files
        })
        .catch(error => {
          console.error('Error:', error);
        });
    },

    deleteFile(fileid) {
      fetch(`/deletefile?fileid=${fileid}`)
        .then(response => response.json())
        .then(data => {
          if (data === 1) {
             this.$message({
                message: "文件删除成功",
                type: "success",
             });
            this.fetchFiles();// 删除成功，刷新文件列表
          } else {
            this.$message({
               message: "文件删除失败",
               type: "success",
            });
            console.error('文件删除失败');
          }
        })
        .catch(error => {
          console.error('Error:', error);
        });
    }
  }
}
</script>

<style>
.file-list {
  max-width: 800px;
  margin: 0 auto;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.search-bar {
  margin-bottom: 20px;
}

.search-bar label {
  margin-right: 10px;
}

.search-bar input {
  padding: 5px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  padding: 10px;
  text-align: center;
  border: 1px solid #ccc;
}

thead {
  background-color: #f5f5f5;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}
</style>