<template>
    <div>
        <el-card class="box-card">
            <h2>添加成绩</h2>
            <el-form
                    :model="ruleForm"
                    status-icon
                    :rules="rules"
                    ref="ruleForm"
                    label-position="left"
                    label-width="80px"
                    class="scoresAdd-form"
            >
                <!-- 表单项：学生ID -->
                <el-form-item label="学生ID" prop="uname">
                    <el-input v-model="ruleForm.uname"></el-input>
                </el-form-item>
                <!-- 表单项：班级 -->
                <el-form-item label="班级ID" prop="class_id">
                    <el-radio-group v-model="ruleForm.class_id">
                        <el-radio :label="1">1班</el-radio>
                        <el-radio :label="2">2班</el-radio>
                        <el-radio :label="3">3班</el-radio>
                    </el-radio-group>
                </el-form-item>
                <!-- 表单项：课程ID -->
                <el-form-item label="课程ID" prop="course_id">
                    <el-radio-group v-model="ruleForm.course_id">
                        <el-radio :label="1">javaweb</el-radio>
                        <el-radio :label="2">java</el-radio>
                    </el-radio-group>
                </el-form-item>
                <!-- 表单项：期末成绩 -->
                <el-form-item label="期末成绩" prop="final_score">
                    <el-input v-model="ruleForm.final_score"></el-input>
                </el-form-item>
                <!-- 表单项：期中成绩 -->
                <el-form-item label="期中成绩" prop="mid_score">
                    <el-input v-model="ruleForm.mid_score"></el-input>
                </el-form-item>
                <!-- 表单项：总成绩 -->
                <el-form-item label="总成绩" prop="total_score">
                    <el-input v-model="ruleForm.total_score"></el-input>
                </el-form-item>
            </el-form>

            <!-- 按钮组 -->
            <div class="btnGroup">
                <!-- 添加成绩按钮 -->
                <el-button type="primary" @click="submitForm('ruleForm')"  v-loading="loading">添加成绩</el-button>
                <!-- 重置按钮 -->
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </div>
        </el-card>
    </div>
</template>

<script>
// 导出 Vue 组件
export default {
  data() {
    return {
      // 表单数据模型
      ruleForm: {
        uname: '',
        class_id: 0,
        course_id: 0,
        final_score: 0,
        mid_score: 0,
        total_score: 0
      },
      // 表单验证规则
      rules: {
        uname: [{ required: true, message: "学生姓名不能为空！", trigger: "blur" },],
        class_id: [{ required: true, message: "班级ID不能为空！", trigger: "blur" },],
        course_id: [{ required: true, message: "课程ID不能为空！", trigger: "blur" },],
        final_score: [{ required: true, message: "期末成绩不能为空！", trigger: "blur" },],
        mid_score: [{ required: true, message: "期中成绩不能为空！", trigger: "blur" },],
        total_score: [{ required: true, message: "总成绩不能为空！", trigger: "blur" },],
      },
      loading: false  // 加载状态
    };
  },

  // 组件的方法
  methods: {
    // 提交表单
    submitForm(formName) {
      // 校验表单数据
      this.$refs[formName].validate((valid) => {
        this.loading = true;  // 提交按钮显示加载动画
        // 表单数据校验通过
        if (valid) {
          let _this = this;
          // 将选中的值转换为数字类型
          _this.ruleForm.class_id = parseInt(_this.ruleForm.class_id);
          // 将选中的值转换为数字类型
          _this.ruleForm.course_id = parseInt(_this.ruleForm.course_id);
          // axios 向后端发起请求
          this.axios({
            method: "post",             // 请求方法
            url: "/api/user/scoreinsert",  // 请求地址
            headers: {                  // 请求头
              "Content-Type": "application/json",
            },
            data: {     // 请求参数
              uname: _this.ruleForm.uname,
              class_id: _this.ruleForm.class_id,
              course_id: _this.ruleForm.course_id,
              final_score: _this.ruleForm.final_score,
              mid_score: _this.ruleForm.mid_score,
              total_score: _this.ruleForm.total_score,
            },
          }).then((res) => { // 当收到后端的响应时执行该括号内的代码，res 为响应信息，也就是后端返回的信息
            if (res === 1) {  // 当响应的编码为 1 时，说明添加成绩成功
              // 显示后端响应的成功信息
              this.$message({
                message: "添加成绩成功",
                type: "success",
              });
            }else{  // 当响应的编码为 0 时，说明添加成绩失败
              // 显示后端响应的失败信息
              this.$message({
                message: "添加成绩失败",
                type: "warning",
              });
            }
            _this.loading = false;  // 不管响应成功还是失败，收到后端响应的消息后就不再让登录按钮显示加载动画了
            console.log(res);
          });
        } else { // 如果字段有一个没填，就直接提示必填，不向后端请求
          console.log("error submit!!");
          this.loading = false;
          return false;
        }
      });
    },
    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style scoped>
/* 设置登录面板居中，宽度为400px */
.box-card {
  margin: auto auto;
  width: 400px;
}
/* 设置登录面板中的表单居中 */
.scoresAdd-form {
  margin: auto auto;
}
</style>